.section-wrapper {
  max-width: 692px;
  width: 100%;
}

.section-top {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  &__container {
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 20;
    display: flex;
    justify-content: flex-end;
    height: 405px;
  }

  .wrapper {
    display: flex;
    justify-content: flex-end;
  }
}

