.title {
  padding: 112px 0 0 0;
  margin: 0;
  text-transform: uppercase;
  color: $crl_title_white;
  font: 90px / normal $fnt_regular;
  font-weight: 700;
  line-height: 0.8;
  /* line-height: 0.67; */
}

.section-subtitle {
  display: flex;
  align-items: center;
  margin: 95px 0 0 0;
  font: 36px / 1.04 $fnt_bold;
  letter-spacing: 1.8px;
  text-transform: uppercase;
  color: $clr_title_gray;
}

.section-title__number {
  color: $clr_black;
  margin-right: 10px;
  font: 18px / 1.1 $fnt_bold;
  border-bottom: 5px solid $clr_purple_border;
  border-radius: 2px;
}

.section-description {
  margin: 30px 0 0 0;
  font: bold 20px / normal $fnt_regular;
  letter-spacing: 0.8px;
  color: $clr_black;
}

.section-middle {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  &__container {
    background-color: $clr_section_gray;
    height: 256px;

    .section-subtitle {
      margin-top: 65px;
    }
  }
}

