.section-middle {
  .wrapper {
    max-width: calc(1920px - 324px * 2);
  }
  .title {
    padding-top: 112px;
  }
  .section-wrapper {
    max-width: 564px;
  }
  .section-top__container {
    height: 443px;
  }

  .section-subtitle {
    margin-top: 0;
    padding-top: 63px;
  }

  .section-middle__container {
    height: 244px;
  }
  .section-bottom__container {
    height: 268px;
    background-color: $clr_shadow;
  }
  .section-bottom__container .section-title__number {
    color: $clr_number_purple;
  }
}

