.section-number__top {
  position: relative;
  top: 0;
  display: flex;
  height: 730px;
  width: 100vw;

  & > img {
    height: 730px;
    width: 50%;
    object-fit: cover;
  }
}

// .section-number__top > img {
//   height: 730px;
//   width: 50%;
//   object-fit: cover;
// }

.section-number__top .section-wrapper,
.section-number__bottom .section-wrapper {
  padding: 0;
  margin: 257px 0 0 127px;
}

.section-number__top .title {
  padding: 0;
}

.section-number__top .section-description,
.section-number__middle .section-description,
.section-number__bottom .section-description {
  max-width: 322px;
  margin-top: 120px;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.17;
  letter-spacing: 1.2px;
}

.section-number__middle {
  width: 100vw;
  position: relative;
  top: 0;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  & > img {
    width: 50%;
    height: 730px;
    object-fit: cover;
  }
}

