//   height: 730px;
//   object-fit: cover;
// }

.section-number__middle {
  .section-description {
    margin-top: 77px;
    max-width: 391px;
  }
  .section-wrapper {
    margin-left: 192px;
    /*margin-left: 20%;*/
  }
}
