.section-number__middle .title {
  padding-top: 257px;
}

.section-number__bottom {
  height: 729px;
  width: 100vw;
  position: relative;
  top: 0;
  display: flex;

  & > img {
    height: 730px;
    width: 50%;
    object-fit: cover;
  }
}

// .section-number__bottom > img {
