//   object-fit: cover;
// }

.section-number__bottom .section-description {
  max-width: 512px;
}

.section-number__bottom .title {
  padding: 0;
}

.block-counter {
  position: absolute;
  top: 210px;
  left: 50vw;
  right: 0;
  transform: translate(-50%, 0);
  width: 168px;
  height: 155px;
  background-color: $clr_square_purple;
}

.inner-info__wrapper {
  padding: 61px 72px 61px 72px;
}

.inner-info {
  height: 32px;
  width: 24px;
  font-weight: bold;
  border-bottom: 5px solid $clr_square_border;
  border-radius: 2px;
  font-size: 24px;
  color: $clr_square_title;
}

#player-football {
  position: absolute;
  top: 41px;
  left: 16.6%;
  right: 0;
  bottom: 0;
  z-index: 50;
}

#player-basket {
  position: absolute;
  top: -30px;
  right: 6.5%;
  bottom: 0;
  z-index: 20;
}

@media all and (max-width: 1920px) {
  .section-number__middle .section-wrapper {
    margin: 0 auto;
  }
}

@media all and (max-width: 1600px) {
  #player-football {
    left: 10%;
  }
}

@media all and (max-width: 1440px) {
  .section-number__middle {
    .title {
      width: min-content;
    }
    .section-wrapper {
      width: fit-content;
    }
  }
  // .section-number__middle .section-wrapper {
  //   width: fit-content;
  // }
  #player-football {
    left: 0%;
  }
  #player-basket {
    right: -15%;
  }
  .section-top .section-wrapper {
    max-width: 592px;
  }
  .section-middle .section-wrapper {
    max-width: 544px;
  }
}

@media all and (max-width: 1200px) {
  #player-football {
    left: -15%;
  }
}

